.preventDesktop {
  display: none;
}
@media only screen and (min-width: 500px) {
  .preventDesktop {
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    background-color: #d2001a;
    width: 100vw;
    height: 100vh;
    z-index: 900;
  }
  .preventDesktop h2 {
    color: #fffae7;
  }
}
