.pandel {
  height: 260px;
  min-width: 170px;
  max-width: 170px;
  border: 2px solid #eb1d36;
  margin-right: 1em;
}
.pandel img {
  width: 100%;
  height: 100px;
}
.pandel a {
  display: block;
  text-decoration: none;
  color: inherit;
  text-align: right;
  margin-bottom: 1em;
  margin-right: 0.3em;
}
.pandel h4 {
  margin: 0;
  padding: 0;
  line-height: 1.2em;
  margin-bottom: 0.5em;
  color: #277bc0;
}
.pandel p {
  line-height: 1.2em;
  color: #483838;
}
.pandelDetails {
  padding: 0.3em;
  overflow: auto;
  height: 145px;
  padding-bottom: 1em;
}

@media only screen and (min-height: 700px) {
  .pandel {
    height: 350px;
    min-width: 200px;
    max-width: 200px;
  }
  .pandel img {
    width: 100%;
    height: 130px;
  }
}
